import { Globals } from '../globals';
import { cardTemplate } from './card.template';
import { Card } from './card.model';

import './card.component.css';

export class CardWebComponent extends HTMLElement {
  constructor() {
    super();

    this.ondragstart = this.onCardDragStart;
    this.draggable = true;
  }

  public connectedCallback() {
    const shadowRoot = this.attachShadow({ mode: 'open' });

    const template = cardTemplate(new Card('Card1', 0));
    shadowRoot.appendChild(this.htmlToElements(template));
  }

  public htmlToElements(html: string) {
    const template = document.createElement('template');
    template.innerHTML = html;
    return template.content!;
  }

  showAddCardForm = (index: number, event: Event): boolean => {
    event.preventDefault();
    event.stopPropagation();

    let boardNode = Globals.boardNode.querySelector(`#list-${index}`)!;
    let formNode = boardNode.querySelector('.form-add-card')! as HTMLElement;
    let titleNode = formNode.querySelector(
      '.inp-card-title'
    )! as HTMLInputElement;

    titleNode.focus();
    formNode.style.display = 'block';

    return false;
  };

  hideAddCardForm = (index: number, event: Event): boolean => {
    event.preventDefault();

    let boardNode = Globals.boardNode.querySelector(`#list-${index}`)!;
    let formNode = boardNode.querySelector('.form-add-card')! as HTMLElement;

    formNode.style.display = 'none';

    return false;
  };

  addCardOnEnter = (index: number, event: KeyboardEvent): boolean => {
    if (event.keyCode === 13) {
      this.addCard(index, event);
    }

    return false;
  };

  addCard = (index: number, event: Event): boolean => {
    event.preventDefault();

    let boardNode = Globals.boardNode.querySelector(`#list-${index}`)!;
    let formNode = boardNode.querySelector('.form-add-card')! as HTMLElement;
    let titleNode = formNode.querySelector(
      '.inp-card-title'
    )! as HTMLInputElement;
    let boardListNone = boardNode.querySelector('.list-cards')! as HTMLElement;

    let card = new Card(titleNode.value, index);

    boardListNone.insertAdjacentHTML('beforeend', cardTemplate(card));

    titleNode.value = '';
    this.hideAddCardForm(index, event);

    return false;
  };

  onCardDragStart = (ev: DragEvent) => {
    let event = ev as DragEvent;
    const target = ev.target as HTMLElement;

    Globals.draggableElement = target;

    event.dataTransfer.setData('text/html', target.innerHTML);
    event.dataTransfer.effectAllowed = 'move';
  };

  onCardDragOver = (ev: Event) => {
    let event = ev as DragEvent;

    event.preventDefault();
    event.dataTransfer.dropEffect = 'move';

    const target = ev.target as HTMLElement;
    const parent = target.parentElement! as HTMLElement;
    const next = target.nextElementSibling as HTMLElement;
    const previous = target.previousElementSibling as HTMLElement;
    const first = parent.firstElementChild! as HTMLElement;
    const last = parent.lastElementChild! as HTMLElement;

    if (
      !Globals.draggableElement.parentElement!.classList.contains('list-cards')
    ) {
      return;
    }

    Globals.draggableElement.style.background = '#C1C8CB';
    let cardConten = Globals.draggableElement.getElementsByClassName(
      'card-content'
    )[0] as HTMLElement;
    cardConten.style.visibility = 'hidden';

    if (!Globals.previousTarget) {
      Globals.previousTarget = target;
    }

    if (Globals.previousTarget == target) {
      return;
    } else {
      Globals.previousTarget = null!;
    }

    if (target && target !== Globals.draggableElement) {
      if (first === Globals.draggableElement.parentElement!) {
        parent.insertBefore(target, Globals.draggableElement);
        target.style.background = '#fff';
      } else if (first === target) {
        parent.insertBefore(Globals.draggableElement, first);
        target.style.background = '#fff';
      } else if (last === Globals.draggableElement) {
        parent.insertBefore(Globals.draggableElement, target);
        target.style.background = '#fff';
      } else if (last === target) {
        if (last.parentElement === Globals.draggableElement.parentElement) {
          parent.insertBefore(last, Globals.draggableElement);
          target.style.background = '#fff';
        } else {
          parent.insertBefore(Globals.draggableElement, last);
          target.style.background = '#fff';
        }
      } else {
        if (Globals.draggableElement == next) {
          parent.insertBefore(Globals.draggableElement, target);
          target.style.background = '#fff';
        } else {
          parent.insertBefore(Globals.draggableElement, next);
          target.style.background = '#fff';
        }
      }
    }
  };

  onCardDragEnd = (ev: Event) => {
    ev.preventDefault();

    let element = ev.target as HTMLElement;

    element.style.background = '#fff';
    let cardConten = element.getElementsByClassName(
      'card-content'
    )[0] as HTMLElement;
    cardConten.style.visibility = 'visible';
  };
}

customElements.define('trello-card', CardWebComponent);



