export class Card {
  title: string;
  descripton: string;
  createdAt: Date;
  updatedAt: Date;
  boardIndex: number;

  constructor(title: string, boardIndex: number) {
    this.title = title;
    this.createdAt = new Date();
    this.updatedAt = new Date();
    this.boardIndex = boardIndex;
  }
}



